import React from "react";
import ListItems from "./ListItems";
import Timer from "./Timer";

// external components import
import { Disable } from "react-disable";
import Modal from "react-modal";

// sound file imports
import finishSound from "./sounds/finish.wav";
import countdownSound from "./sounds/countdownBegins.wav";
import lastSecond from "./sounds/lastSecondBeep.wav";
import lastSeconds from "./sounds/lastSecondsBeep.wav";

Modal.setAppElement("#root");

// define the App class-based component
class App extends React.Component {
  state = {
    modalMessage: "",
    showModal: false,
    startCountdown: true,
    isCounting: false,
    blocking: false,
    items: [],
    totalDurationInSeconds: 0,
    totalDisplayedDurationInSeconds: 0,
    currentItem: {
      exerciseText: "",
      textColor: "",
      backgroundColor: "",
      beepBeforeEnd: 0,
      minutes: 0,
      seconds: 0,
      displayedMinutes: 0,
      displayedSeconds: 0,
      key: "",
      isSelected: false,
      isCounting: false,
    },
  };

  // modal funcs ******************
  handleOpenModal() {
    this.setState({ showModal: true });
  }
  handleCloseModal() {
    const modalTimer = setTimeout(() => {
      this.setState({ showModal: false, modalMessage: "" });
      clearTimeout(modalTimer);
    }, 3000);
  }

  // audio funcs ******************
  playAudio() {
    const audioEl = document.getElementsByClassName("audio-element")[0];
    audioEl.play();
  }
  playAudioCountdown() {
    const audioEl = document.getElementsByClassName("audio-element")[1];
    audioEl.play();
  }
  playAudioLastSeconds() {
    const audioEl = document.getElementsByClassName("audio-element")[2];
    audioEl.play();
  }
  playAudioLastSecond() {
    const audioEl = document.getElementsByClassName("audio-element")[3];
    audioEl.play();
  }

  // handle changes - coulda written them in jsx but this is ok too
  // also adding unique key in this action
  handleChangeText = (e) => {
    // console.log(e.target.value);
    this.setState({
      currentItem: {
        ...this.state.currentItem,
        exerciseText: e.target.value,
        key: Date.now(),
      },
    });
  };
  handleChangeSeconds = (e) => {
    // console.log(e.target.value);
    this.setState({
      currentItem: {
        ...this.state.currentItem,
        seconds: parseInt(e.target.value),
        displayedSeconds: parseInt(e.target.value),
      },
    });
  };
  handleChangeMinutes = (e) => {
    // console.log(e.target.value);
    this.setState({
      currentItem: {
        ...this.state.currentItem,
        minutes: parseInt(e.target.value),
        displayedMinutes: parseInt(e.target.value),
      },
    });
  };

  // toggle ui blocking
  turnBlockingOn = () => {
    this.setState({ blocking: true });
  };
  turnBlockingOff = () => {
    this.setState({ blocking: false });
  };

  // validation func.
  _helperValidation = () => {
    if (!this.state.currentItem.exerciseText) {
      this.setState({ modalMessage: "Exercise must have a name" }, () => {
        this.handleOpenModal();
        this.handleCloseModal();
      });
      return false;
    }

    if (
      isNaN(Number(this.state.currentItem.displayedMinutes)) ||
      isNaN(Number(this.state.currentItem.displayedSeconds)) ||
      this.state.currentItem.displayedMinutes === "" ||
      this.state.currentItem.displayedSeconds === ""
    ) {
      this.setState(
        { modalMessage: "Only numbers accepted in Seconds and Minutes inputs" },
        () => {
          this.handleOpenModal();
          this.handleCloseModal();
        }
      );
      return false;
    }

    if (
      !(
        this.state.currentItem.displayedMinutes >= 0 &&
        this.state.currentItem.minutes <= 59
      )
    ) {
      this.setState(
        { modalMessage: "Minutes input is not within 0-59 range" },
        () => {
          this.handleOpenModal();
          this.handleCloseModal();
        }
      );
      return false;
    }

    if (
      !(
        this.state.currentItem.displayedSeconds >= 0 &&
        this.state.currentItem.displayedSeconds <= 59
      )
    ) {
      this.setState(
        { modalMessage: "Seconds input is not within 0-59 range" },
        () => {
          this.handleOpenModal();
          this.handleCloseModal();
        }
      );
      return false;
    }

    if (
      !(
        this.state.currentItem.displayedMinutes > 0 ||
        this.state.currentItem.displayedSeconds > 0
      )
    ) {
      this.setState(
        { modalMessage: "Overall time has to be more than 1 second." },
        () => {
          this.handleOpenModal();
          this.handleCloseModal();
        }
      );
      return false;
    }
    return true;
  };

  // duplicate item and add at bottom
  duplicateItem = (key) => {
    const items = this.state.items;
    items.map((item) => {
      if (item.key === key) {
        // console.log(item.key + "    " + key);
        const dupeItem = { ...item };
        dupeItem.key = Date.now();
        items.push(dupeItem);
      }
    });
    this.setState(
      {
        items: items,
      },
      this.totalTimeCalculation
    );
  };

  // delete item func.
  deleteItem = (key) => {
    const filteredItems = this.state.items.filter((item) => item.key !== key);
    this.setState(
      {
        items: filteredItems,
      },
      this.totalTimeCalculation
    );
  };

  // update item in list func.
  setUpdate = (text, key) => {
    // console.log("items:" + this.state.items);
    const items = this.state.items;
    items.map((item) => {
      if (item.key === key) {
        // console.log(item.key + "    " + key);
        item.exerciseText = text;
      }
    });
    this.setState({
      items: items,
    });
  };

  // func to turn minutes and seconds into seconds
  // also updates state - used to circumvent one step behind issue
  totalTimeCalculation = () => {
    let totalTimeInSeconds = 0;
    let totalDisplayedTimeInSeconds = 0;
    for (const item of this.state.items) {
      totalTimeInSeconds += item.seconds + item.minutes * 60;
      totalDisplayedTimeInSeconds +=
        item.displayedSeconds + item.displayedMinutes * 60;
    }
    // convert to hh:mm:ss format
    // return new Date(totalTimeInSeconds * 1000).toISOString().substr(11, 8);
    this.setState({
      totalDurationInSeconds: totalTimeInSeconds,
      totalDisplayedDurationInSeconds: totalDisplayedTimeInSeconds,
    });
  };

  // helper func for countdown timer individual
  countdownInterval = (arrayIndex, itemsArray) => {
    if (this.state.totalDisplayedDurationInSeconds !== 0) {
      this.myInterval = setInterval(() => {
        if (arrayIndex < itemsArray.length) {
          const item = itemsArray[arrayIndex];
          let { displayedSeconds, displayedMinutes } = item;

          if (displayedSeconds > 0) {
            item.displayedSeconds = displayedSeconds - 1;
            item.isCounting = true;
            item.isSelected = true;
            if (displayedMinutes === 0 && displayedSeconds === 1) {
              this.playAudioLastSecond();
            } else if (displayedMinutes === 0 && displayedSeconds < 5) {
              this.playAudioLastSeconds();
            }
            this.setState({
              items: itemsArray,
            });
            this.totalTimeCalculation();
          }

          if (displayedSeconds === 0) {
            if (displayedMinutes === 0) {
              item.isCounting = false;
              item.isSelected = false;
              // this.playAudioLastSecond();
              this.setState({
                items: itemsArray,
              });
              this.totalTimeCalculation();
              clearInterval(this.myInterval);
              if (itemsArray[arrayIndex + 1]) {
                this.countdownInterval(arrayIndex + 1, itemsArray);
              } else {
                // code here for when displayed seconds = 0;
                this.setState({
                  isCounting: false,
                  startCountdown: false,
                });
                this.playAudio();
              }
            } else {
              item.displayedMinutes = displayedMinutes - 1;
              item.displayedSeconds = 59;
              item.isCounting = true;
              item.isSelected = true;
              this.setState({
                items: itemsArray,
              });
              this.totalTimeCalculation();
            }
          }
        } else {
          clearInterval(this.myInterval);
        }
      }, 1000);
    }
  };

  // func to start counter for items from Timer component
  startCountdownItem = () => {
    const items_ = this.state.items;
    let counter = 0;
    let soundCountdownCounter = 0;

    if (
      !this.state.isCounting &&
      this.state.startCountdown &&
      this.state.totalDurationInSeconds !== 0
    ) {
      const timeout = setTimeout(() => {
        this.countdownInterval(counter, items_);
      }, 3000);

      const timeBeep = setInterval(() => {
        this.playAudioCountdown();
        soundCountdownCounter++;
        if (soundCountdownCounter === 3) {
          clearInterval(timeBeep);
          clearTimeout(timeout);
        }
      }, 1010);
    } else if (!this.state.isCounting) {
      this.setState(
        {
          isCounting: true,
          startCountdown: false,
        },
        this.countdownInterval(counter, items_)
      );
    }
  };

  // func to stop counter for current item - which also stops display below from dropping
  stopCountdownItem = () => {
    if (this.myInterval) {
      clearInterval(this.myInterval);
      this.setState({
        isCounting: false,
        startCountdown: false,
      });
    }
  };

  // timer function - minutes and seconds - and hours
  resetTimer = () => {
    if (this.myInterval) {
      clearInterval(this.myInterval);

      const items = this.state.items;
      for (const item of items) {
        item.displayedSeconds = item.seconds;
        item.displayedMinutes = item.minutes;
        item.isSelected = false;
        item.isCounting = false;
      }
      this.setState({
        items: items,
        isCounting: false,
        startCountdown: true,
      });
      this.totalTimeCalculation();
    }
  };

  // func to determine what happens when Add button is pressed
  handleAddButon = () => {
    // validation check first
    const validation = this._helperValidation();
    if (validation) {
      // add new item to items array
      const newItem = this.state.currentItem;
      const new_items = [...this.state.items, newItem];

      // add to list and reset new item -- also update state again using callback
      this.setState(
        {
          items: new_items,
          currentItem: {
            ...this.state.currentItem,
            exerciseText: "",
            minutes: 0,
            seconds: 0,
            displayedMinutes: 0,
            displayedSeconds: 0,
            key: "",
          },
        },
        this.totalTimeCalculation
      );
    } else {
      console.log("Error - Validation did not pass - retry the form");
    }
  };

  // App render - html **************************
  render() {
    return (
      <div className="App">
        <h1 className="title">Super Sports Timer</h1>

        <div
          style={{ visibility: this.state.showModal ? "visible" : "hidden" }}
          className="modal"
        >
          {this.state.modalMessage}
        </div>

        {/* Audio divs for loading sounds */}
        <div style={{ visibility: "hidden" }}>
          <div style={{ visibility: "hidden" }}>
            <audio src={finishSound} className="audio-element"></audio>
          </div>
          <div style={{ visibility: "hidden" }}>
            <audio src={countdownSound} className="audio-element"></audio>
          </div>
          <div style={{ visibility: "hidden" }}>
            <audio src={lastSeconds} className="audio-element"></audio>
          </div>
          <div style={{ visibility: "hidden" }}>
            <audio src={lastSecond} className="audio-element"></audio>
          </div>
        </div>

        <div className="container">
          <div className="first-row">
            <div className="item-content">
              <label>Exercise Name</label>
            </div>
            <div className="item-content">
              <label>Minutes</label>
            </div>
            <div className="item-content">
              <label>Seconds</label>
            </div>
            <div className="item-content">
              <label>Add</label>
            </div>
          </div>

          <Disable disabled={this.state.blocking}>
            <div className="second-row">
              <div className="item-content">
                <input
                  type="text"
                  maxLength="20"
                  value={this.state.currentItem.exerciseText}
                  onChange={this.handleChangeText}
                />
              </div>
              <div className="item-content">
                <input
                  type="number"
                  min="0"
                  max="59"
                  value={this.state.currentItem.displayedMinutes}
                  onChange={this.handleChangeMinutes}
                />
              </div>
              <div className="item-content">
                <input
                  type="number"
                  min="0"
                  max="59"
                  value={this.state.currentItem.displayedSeconds}
                  onChange={this.handleChangeSeconds}
                />
              </div>
              <div className="item-content">
                <button onClick={this.handleAddButon} type="button">
                  <i className="fa fa-plus addition"></i>
                </button>
              </div>
            </div>
          </Disable>

          <ListItems
            startCountdown={this.state.startCountdown}
            items={this.state.items}
            deleteItem={this.deleteItem}
            setUpdate={this.setUpdate}
            blocking={this.state.blocking}
            duplicateItem={this.duplicateItem}
          />

          <Timer
            items={this.state.items}
            turnBlockingOn={this.turnBlockingOn}
            turnBlockingOff={this.turnBlockingOff}
            totalDurationInSeconds={this.state.totalDurationInSeconds}
            totalDisplayedDurationInSeconds={
              this.state.totalDisplayedDurationInSeconds
            }
            startCountdownItem={this.startCountdownItem}
            stopCountdownItem={this.stopCountdownItem}
            resetTimer={this.resetTimer}
          />
        </div>
      </div>
    );
  }
}

export default App;

