import React from "react";

class Timer extends React.Component {
  state = {
    isCounting: false,
    seconds: 0,
    minutes: 0,
    hours: 0,
    displayedSeconds: 0,
    displayedMinutes: 0,
    displayedHours: 0,
  };

  // helper func to get this duration and turn into h - m - s
  _secondsToFormat = () => {
    let seconds = this.props.totalDurationInSeconds;
    let secondsDisplayed = this.props.totalDisplayedDurationInSeconds;

    let hours = Math.floor(seconds / 3600);
    let hoursDisplayed = Math.floor(secondsDisplayed / 3600);

    seconds = seconds - hours * 3600;
    secondsDisplayed = secondsDisplayed - hoursDisplayed * 3600;

    let minutes = Math.floor(seconds / 60);
    let minutesDisplayed = Math.floor(secondsDisplayed / 60);

    seconds = seconds - minutes * 60;
    secondsDisplayed = secondsDisplayed - minutesDisplayed * 60;
    this.setState({
      seconds: seconds || 0,
      minutes: minutes || 0,
      hours: hours || 0,
      displayedSeconds: secondsDisplayed || 0,
      displayedMinutes: minutesDisplayed || 0,
      displayedHours: hoursDisplayed || 0,
    });
  };

  // this updates seconds when item is added or deleted;
  // without it we are one step behind
  componentDidUpdate(prevProps) {
    if (
      prevProps.totalDurationInSeconds !== this.props.totalDurationInSeconds ||
      prevProps.totalDisplayedDurationInSeconds !==
        this.props.totalDisplayedDurationInSeconds
    ) {
      this._secondsToFormat();
    }
  }

  render() {
    return (
      <div style={{ paddingBottom: "0.4em" }}>
        <div className="last-row">
          <div className="player" style={{ left: "0", position: "absolute" }}>
            <button
              onClick={() => {
                this.props.startCountdownItem();
                this.props.turnBlockingOn();
              }}
            >
              <i className="play icon small"></i>
            </button>
            <button onClick={this.props.stopCountdownItem}>
              <i className="pause icon small"></i>
            </button>
            <button
              onClick={() => {
                this.props.resetTimer();
                this.props.turnBlockingOff();
              }}
            >
              <i className="undo icon small"></i>
            </button>
          </div>
          <div id="timer">
            <h1>
              Countdown:{" "}
              {`${
                this.state.displayedHours < 10
                  ? "0" + this.state.displayedHours
                  : this.state.displayedHours
              }:${
                this.state.displayedMinutes < 10
                  ? "0" + this.state.displayedMinutes
                  : this.state.displayedMinutes
              }:${
                this.state.displayedSeconds < 10
                  ? "0" + this.state.displayedSeconds
                  : this.state.displayedSeconds
              }`}
            </h1>
          </div>
          <h5
            style={{
              margin: "0px",
              marginRight: "0.5em",
              position: "absolute",
              right: "0",
              color: "rgb(86, 207, 225)",
            }}
          >
            Total:{" "}
            {`${
              this.state.hours < 10 ? "0" + this.state.hours : this.state.hours
            }:${
              this.state.minutes < 10
                ? "0" + this.state.minutes
                : this.state.minutes
            }:${
              this.state.seconds < 10
                ? "0" + this.state.seconds
                : this.state.seconds
            }`}
          </h5>
        </div>
      </div>
    );
  }
}

export default Timer;

