import React from "react";
import { VoicePlayer } from "react-voice-components";
import { Disable } from "react-disable";

const ListItems = (props) => {
  const items = props.items;
  const listItems = items.map((item) => {
    return (
      <div
        className={"activity " + (item.isSelected ? "active" : "")}
        key={item.key}
      >
        {item.isSelected ? <VoicePlayer play text={item.exerciseText} /> : ""}
        <div className="activity-duration">
          {`${
            item.displayedMinutes < 10
              ? "0" + item.displayedMinutes
              : item.displayedMinutes
          }:${
            item.displayedSeconds < 10
              ? "0" + item.displayedSeconds
              : item.displayedSeconds
          }`}
        </div>

        <div className="activity-name">
          <input
            type="text"
            maxLength="20"
            // id={item.key}
            value={item.exerciseText}
            onChange={(e) => {
              props.setUpdate(e.target.value, item.key);
            }}
          />
        </div>
        <Disable disabled={props.blocking}>
          <button
            className="duplicate"
            onClick={() => {
              props.duplicateItem(item.key);
            }}
          >
            <i className="fa fa-plus"></i>
          </button>
          <button
            className="remove"
            onClick={() => {
              props.deleteItem(item.key);
            }}
          >
            <i className="fa fa-trash"></i>
          </button>
        </Disable>
      </div>
    );
  });
  return <div className="third-row">{listItems}</div>;
};

export default ListItems;
